import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import axios from 'axios';

const SettingsContainer = styled.div`
  padding: 1rem;
`;

const SettingsTitle = styled.h1`
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1.5rem;
`;

const SettingsDescription = styled.p`
  color: #666;
  margin-bottom: 2rem;
`;

const SiteListContainer = styled.div`
  margin-bottom: 2rem;
`;

const SiteListHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
`;

const SiteListTitle = styled.h2`
  font-size: 1.4rem;
  color: #333;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 0.5rem;
`;

const Button = styled.button`
  padding: 0.5rem 1rem;
  background-color: ${props => props.primary ? '#4a6da7' : '#f0f0f0'};
  color: ${props => props.primary ? 'white' : '#333'};
  border: 1px solid ${props => props.primary ? '#4a6da7' : '#ddd'};
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  
  &:hover {
    background-color: ${props => props.primary ? '#3a5d97' : '#e0e0e0'};
  }
`;

const SiteList = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;

const SiteItem = styled.div`
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 1rem;
  display: flex;
  align-items: center;
  background-color: ${props => props.selected ? '#f0f7ff' : 'white'};
  border-color: ${props => props.selected ? '#4a6da7' : '#ddd'};
`;

const Checkbox = styled.input`
  margin-right: 0.8rem;
  width: 18px;
  height: 18px;
  cursor: pointer;
`;

const SiteName = styled.label`
  font-size: 1rem;
  cursor: pointer;
`;

const SaveButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 2rem;
`;

const SaveButton = styled(Button)`
  padding: 0.8rem 2rem;
  font-size: 1rem;
`;

const LoadingMessage = styled.div`
  text-align: center;
  padding: 2rem;
  font-size: 1.2rem;
  color: #666;
`;

const ErrorMessage = styled.div`
  text-align: center;
  padding: 1rem;
  font-size: 1rem;
  color: #d9534f;
  background-color: #f9f2f2;
  border-radius: 4px;
  margin-bottom: 1rem;
`;

const SuccessMessage = styled.div`
  text-align: center;
  padding: 1rem;
  font-size: 1rem;
  color: #28a745;
  background-color: #f0fff4;
  border-radius: 4px;
  margin-bottom: 1rem;
`;

// APIURL
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

const SettingsPage = () => {
  const [sites, setSites] = useState([]);
  const [selectedSites, setSelectedSites] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  
  // 
  useEffect(() => {
    const fetchSites = async () => {
      setLoading(true);
      setError(null);
      
      try {
        // API
        // API
        /*
        const response = await axios.get(`${API_BASE_URL}/sites`);
        setSites(response.data);
        */
        
        // 
        setTimeout(() => {
          const mockSites = [
            { id: '1', name: '', url: 'https://pointi.jp/', active: true, priority: 10 },
            { id: '2', name: '', url: 'https://hapitas.jp/', active: true, priority: 9 },
            { id: '3', name: '', url: 'https://moppy.jp/', active: true, priority: 8 },
            { id: '4', name: 'EC', url: 'https://ecnavi.jp/', active: true, priority: 7 },
            { id: '5', name: '', url: 'https://amefri.jp/', active: true, priority: 6 },
            { id: '6', name: '', url: 'https://www.warau.jp/', active: true, priority: 5 },
            { id: '7', name: '', url: 'https://pointtown.com/', active: true, priority: 4 },
            { id: '8', name: '', url: 'https://point.nifty.com/', active: true, priority: 3 },
            { id: '9', name: 'GetMoney!', url: 'https://getmoney.jp/', active: true, priority: 2 },
            { id: '10', name: 'Powl', url: 'https://powl.jp/', active: true, priority: 1 },
            { id: '11', name: '', url: 'https://www.chobirich.com/', active: true, priority: 0 },
            { id: '12', name: '', url: 'https://www.gendama.jp/', active: true, priority: 0 }
          ];
          setSites(mockSites);
          
          // 
          const savedSites = localStorage.getItem('selectedSites');
          if (savedSites) {
            setSelectedSites(JSON.parse(savedSites));
          } else {
            // 
            setSelectedSites(mockSites.map(site => site.id));
          }
          
          setLoading(false);
        }, 1000);
        
      } catch (err) {
        console.error('Error fetching sites:', err);
        setError('');
        setLoading(false);
      }
    };
    
    fetchSites();
  }, []);
  
  // 
  const handleSiteToggle = (siteId) => {
    setSelectedSites(prevSelected => {
      if (prevSelected.includes(siteId)) {
        return prevSelected.filter(id => id !== siteId);
      } else {
        return [...prevSelected, siteId];
      }
    });
  };
  
  // 
  const selectAll = () => {
    setSelectedSites(sites.map(site => site.id));
  };
  
  // 
  const deselectAll = () => {
    setSelectedSites([]);
  };
  
  // 
  const saveSettings = () => {
    try {
      // 
      localStorage.setItem('selectedSites', JSON.stringify(selectedSites));
      
      // API
      /*
      await axios.post(`${API_BASE_URL}/settings`, {
        sites: selectedSites
      });
      */
      
      setSuccessMessage('');
      setTimeout(() => {
        setSuccessMessage(null);
      }, 3000);
    } catch (err) {
      console.error('Error saving settings:', err);
      setError('');
    }
  };
  
  return (
    <SettingsContainer>
      <SettingsTitle></SettingsTitle>
      <SettingsDescription>
        
      </SettingsDescription>
      
      {successMessage && (
        <SuccessMessage>{successMessage}</SuccessMessage>
      )}
      
      {error && (
        <ErrorMessage>{error}</ErrorMessage>
      )}
      
      {loading ? (
        <LoadingMessage>...</LoadingMessage>
      ) : (
        <>
          <SiteListContainer>
            <SiteListHeader>
              <SiteListTitle></SiteListTitle>
              <ButtonGroup>
                <Button onClick={selectAll}></Button>
                <Button onClick={deselectAll}></Button>
              </ButtonGroup>
            </SiteListHeader>
            
            <SiteList>
              {sites.map(site => (
                <SiteItem 
                  key={site.id} 
                  selected={selectedSites.includes(site.id)}
                >
                  <Checkbox
                    type="checkbox"
                    id={`site-${site.id}`}
                    checked={selectedSites.includes(site.id)}
                    onChange={() => handleSiteToggle(site.id)}
                  />
                  <SiteName htmlFor={`site-${site.id}`}>
                    {site.name}
                  </SiteName>
                </SiteItem>
              ))}
            </SiteList>
          </SiteListContainer>
          
          <SaveButtonContainer>
            <SaveButton primary onClick={saveSettings}>
              
            </SaveButton>
          </SaveButtonContainer>
        </>
      )}
    </SettingsContainer>
  );
};

export default SettingsPage;
